@import url(https://fonts.googleapis.com/css?family=Roboto:400,500);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  height: 100vh;
  overflow-y: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}


.icon-twotone {
  white-space: nowrap;
}

.icon-twotone svg {
  fill: #909191;
}
.icon-twotone.yellow svg {
  fill: #f4b400;
}
.icon-twotone.blue svg {
  fill: #4285f4;
}

.icon-twotone.hover {
  border-radius: 49%;
  -webkit-transition: all .2s;
  transition: all .2s;
  height: 36px;
  width: 36px;
  margin: -6px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.icon-twotone.hover:hover {
  background-color: #f6f7f8;
}

.grid {
  display: grid;
  overflow-y: hidden;
  grid-template-columns: minmax(216px, 256px) minmax(320px, 360px) minmax(640px,1fr) 50px;
  height: calc(100vh - 64px);
}

.header {
  height: 64px;
  padding: 0 30px;
  border-bottom: 1px solid #e4e8eb;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.header--bar {
  margin-left: auto;
  display: flex;
  align-items: center;
}

.header--user {
  cursor: pointer;
  width: 34px;
  height: 34px;
  border-radius: 49%;
  background: url("https://randomuser.me/api/portraits/men/46.jpg") no-repeat;
  background-size: cover;
  background-position: center;
}

.header--notification {
  margin: 5px 25px 0px;
  cursor: pointer;
}

.header--notification:hover svg {
  fill: #000000;
}

.header--apps {
  cursor: pointer;
  margin-top: 5px;
}

.twotone-icon {
  flex: 0 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.twotone-icon .twotone-icon--hoverable {
  border-radius: 49%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.twotone-icon:hover .twotone-icon--hoverable {
  background-color: #eeeef1;
}
.twotone-icon:hover .twotone-icon--hoverable svg {
  fill: #666666;
}
.twotone-icon svg {
  fill: #909191;
}
.twotone-icon.yellow svg {
  fill: #f4b400;
}
.twotone-icon.blue svg {
  fill: #4285f4;
}

.search {
  position: relative;
  margin-left: 117px;
}

.search--icon {
  position: absolute;
  top: 10px;
  left: 12px;
  color: #909191;
}

.search--drop_down {
  position: absolute;
  top: 10px;
  right: 12px;
  color: #909191;
  cursor: pointer;
}

.search > input {
  box-sizing: border-box;
  width: 250px;
  background-color: #f7f8f9;
  border: 1px solid transparent;
  border-radius: 3px;
  font-size: 16px;
  padding: 12px 45px;
  color: #909191;
  font-weight: 400;
  outline: none;
  color: #000;
}

@media screen and (min-width: 940px) {
  .search > input {
    width: 500px;
  }
}

.search > input:focus {
  background-color: #ffffff;
  border-color: #e4e8eb;
}

.sidebar {
  max-width: 256px;
  height: calc(100vh - 64px);
  border-right: 1px solid #e4e8eb;
}

.sidebar--compose {
  outline: none;
  display: inline-flex;
  align-items: center;
  background-color: #ffffff;
  border: none;
  margin: 18px 0 0 14px;
  font-size: 12px;
  text-transform: uppercase;
  line-height: 16px;
  font-weight: 600;
  box-shadow: 0 3px 10px #e4e8eb;
  padding: 10px 35px 10px 14px;
  border-radius: 26px;
  cursor: pointer;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.sidebar--compose:hover {
  box-shadow: 0 1px 3px 0 rgba(60,64,67,0.302), 0 4px 8px 3px rgba(60,64,67,0.149);
  background-color: #fafafb;
}
.sidebar--compose img {
  margin-right: 17px;
}

.sidebar--sections {
  margin: 26px 0 11px;
}

.sidebar--item {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 8px 23px 8px 32px;
  color: #909191;
  font-size: 14px;
  line-height: 1;
  font-weight: 500;
  text-decoration: none;
  border-left: 3px solid transparent;
}

.sidebar--item .twotone-icon svg {
  margin-right: 22px;
}
.sidebar--unreaded-counter {
  margin-left: auto;
  font-size: 12px;
  font-weight: 400;

}
.sidebar--item:not(.sidebar--item-active):hover {
  background-color: rgba(144, 145, 145, .1);
  border-color: rgb(144,145,145);
}

.sidebar--item-active {
  color: #db4437;
  border-color: #db4437;
  background-color: rgba(219,68,55, .1);
}

.sidebar--item-active .twotone-icon svg {
  fill: #db4437;
}

.sidebar--delimiter {
  height: 1px;
  background-color: #e4e8eb;
  margin: 0 20px;
}

.sidebar--options {
  margin: 26px 0;
}

.folders {
  margin-bottom: 21px;
}

.folders--heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 23px 22px 10px 32px;
  color: #909191;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.folders--heading i {
  font-size: 16px;
}

.folders--add {
  cursor: pointer;
}

.folders--item {
  display: flex;
  cursor: pointer;
  align-items: center;
  box-sizing: border-box;
  padding: 8px 23px 8px 32px;
  color: #909191;
  font-size: 14px;
  line-height: 1;
  font-weight: 500;
  text-decoration: none;
  border-left: 3px solid transparent;
}

.folders--item .twotone-icon  {
  margin-right: 22px;
  height: 18px;
}

.folders--item-active {
  color: #000000;
}

.folders--item:hover {
  color: #000000;
}

.services {
  padding-top: 20px;
}

.services--item {
  cursor: pointer;
  width: 20px;
  height: 20px;
  margin: 0 auto 30px;
  background-repeat: no-repeat;
  background-size: contain;
}

.services--delimiter {
  width: 22px;
  height: 1px;
  margin: 0 auto 33px;
  background-color: #e4e8eb;
}

.services--add {
  cursor: pointer;
  text-align: center;
}

.loader {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader--logo {
  width: 200px;
  height: 200px;
  background-repeat: no-repeat;
  background-size: contain;
  -webkit-animation: blink 2s ease-in-out infinite;
          animation: blink 2s ease-in-out infinite;
}

.loader--message {
  color: #909191;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: .1px;
  opacity: .8;
  font-weight: 400;
  text-align: center;
}

@-webkit-keyframes blink {
  0% {
    opacity: .1;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: .1;
  }
}

@keyframes blink {
  0% {
    opacity: .1;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: .1;
  }
}

.notification {
  position: fixed;
  z-index: 100;
  bottom: 20px;
  left: 20px;
  border-radius: 5px;
  background-color: #212223;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.14);
  color: #ffffff;
  font-size: 14px;
  font-weight: 500;
  padding: 15px 25px;
  -webkit-animation: appear .5s;
          animation: appear .5s;
}


@-webkit-keyframes appear{
    from {
      left: -300px;
      bottom: -300px;
      opacity: .1;
    }
    to{
      left: 20px;
      bottom: 20px;
      opacity: 1;
    }
}


@keyframes appear{
    from {
      left: -300px;
      bottom: -300px;
      opacity: .1;
    }
    to{
      left: 20px;
      bottom: 20px;
      opacity: 1;
    }
}

.conversations--bar {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #e4e8eb;
  color: #909191;
  padding: 0 18px;
}
.conversations--bar-actions {
  display: flex;
  align-items: center;
}

.conversations--bar .conversations--actions {
  display: flex;
  margin-top: 7px;
}
.conversations--bar .conversations--actions .twotone-icon {
  margin-left: 18px;
}
.conversations--checkAll {
  margin-top: 6px;
}
.conversations--more {
  margin-left: 18px;
}

.conversations--not-found {
  color: #909191;
  font-size: 14px;
  text-align: center;
  margin-top: 3em;
}

.conversations--item {
  text-decoration: none;
  position: relative;
  display: flex;
  align-items: flex-start;
  padding: 20px 16px 20px 13px;
  border-left: 3px solid transparent;
}

.conversations--item:not(.selected):hover .conversations--datetime {
  opacity: 0;
}
.conversations--item:not(.selected):hover .conversations--actions {
  display: flex;
}
.conversations--item:not(.selected):hover .conversations--starred {
  display: block;
}

.conversations--item.selected,
.conversations--item.active {
  background-color: #f7f8f9;
  border-left: 3px solid #db4437;
}
.conversations--item.active .conversations--name {
  color: #212223;
}
.conversations--item.active .conversations--starred {
  display: block;
}
.conversations--item.active .conversations--last-message {
  color: #909191 !important;
}


.conversations--item.selected {
  color: #909191;
}
.conversations--item.selected .conversations--name {
  color: #909191 !important;
}
.conversations--item.selected .conversations--last-message {
  color: #909191 !important;
  opacity: 0.8;
}
.conversations--item.selected .conversations--avatar {
  background-color: #db4437;
}

.conversations--avatar {
  flex: 0 0 40px;
  width: 40px;
  height: 40px;
  border-radius: 49%;
  background-color: #b3b3b3;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin-right: 11px;
  cursor: pointer;
}
.conversations--avatar.no-avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 1;
}
.conversations--avatar.no-avatar.blue {
  background-color: rgba(66, 133, 244, 0.2);
  border: 1px solid #4285f4;
  color: #4285f4;
}
.conversations--avatar.no-avatar.yellow {
  background-color: rgba(244, 180, 0, 0.2);
  border: 1px solid #f4b400;
  color: #f4b400;
}
.conversations--avatar.no-avatar.green {
  background-color: rgba(15, 157, 88, 0.2);
  border: 1px solid #0f9d58;
  color: #0f9d58;
}
.conversations--avatar.conversations--avatar-selected {
  background-color: #db4437;
  display: flex;
  align-items: center;
  justify-content: center;
}
.conversations--avatar.conversations--avatar-selected svg {
  fill: #ffffff;
}

.conversations--data {
  overflow: hidden;
  cursor: pointer;
}

.conversations--item.new .conversations--name {
  color: #212223;
}

.conversations--name {
  margin: 0;
  color: #909191;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
}

.conversations--item.new .conversations--last-message {
  color: #212223;
}

.conversations--last-message {
  color: #909191;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-size: 13px;
  font-weight: 400;
  line-height: 20px;
  margin: 0;
}

.conversations--datetime {
  margin: 0;
  margin-left: auto;
  font-size: 12px;
  white-space: nowrap;
  color: #909191;
  text-transform: uppercase;
  font-weight: 400;
  opacity: 0.8;
}

.conversations--item .conversations--actions {
  position: absolute;
  display: none;
  right: 16px;
  top: 15px;
}
.conversations--item .conversations--actions .twotone-icon {
  margin-left: 18px;
}

.conversations--starred {
  display: none;
  position: absolute;
  bottom: 12px;
  right: 16px;
}

.conversations--animation-leave {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.conversations--animation-leave.conversations--animation-leave-active {
  opacity: 0.01;
  -webkit-transform: translateX(-360px);
          transform: translateX(-360px);
  -webkit-transition: all 300ms ease-in;
  transition: all 300ms ease-in;
}

.conversation {
  background-color: #f7f8f9;
  
}

.dialog--header {
  height: 60px;
  display: flex;
  align-items: center;
  border: 1px solid #e4e8eb;
  border-top: none;
  background-color: #ffffff;
  padding: 0 35px 0 40px;
}

.dialog--title {
  color: #212223;
  font-size: 18px;
  line-height: 28px;
  font-weight: 400;
  margin: 0;
}

.dialog--no-messages {
  color: #909191;
  font-size: 14px;
  text-align: center;
  margin-top: 3rem;
}

.dialog--labels {
  margin-left: 25px;
}

.dialog--body {
  padding: 20px;
}

.dialog--bar {
  margin-left: auto;
  display: flex;
  align-items: center;
}

.dialog--action {
  padding-top: 8px;
  margin-left: 24px;
  cursor: pointer;
}

.dialog--scroll {
  max-height: 100%;
}

.message {
  background-color: #ffffff;
  margin-bottom: 10px;
  border: 1px solid #e4e8eb;
  border-radius: 3px;
  position: relative;
}

.message--content {
  display: flex;
  align-items: flex-start;
  padding: 20px 16px;
}

.message--additional {
  display: flex;
  align-items: center;
  padding-top: 4px;
}

.message--avatar {
  flex: 0 0 40px;
  height: 40px;
  width: 40px;
  border-radius: 49%;
  background-size: cover;
  background-position: center;
  margin-right: 11px;
}
.message--avatar:hover .contact-info {
  max-width: 700px;
  max-height: 480px;
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 1px 18px rgba(0, 0, 0, 0.12), 0 6px 10px rgba(0, 0, 0, 0.14);
}

.message--avatar.no-avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 1;
}
.message--avatar.no-avatar.blue {
  background-color: rgba(66, 133, 244, 0.2);
  border: 1px solid #4285f4;
  color: #4285f4;
}
.message--avatar.no-avatar.yellow {
  background-color: rgba(244, 180, 0, 0.2);
  border: 1px solid #f4b400;
  color: #f4b400;
}
.message--avatar.no-avatar.green {
  background-color: rgba(15, 157, 88, 0.2);
  border: 1px solid #0f9d58;
  color: #0f9d58;
}

.message--body {
  overflow-x: hidden;
  cursor: pointer;
  flex: 1 0;
}
.message--body.selected {
  cursor: auto;
}

.message--username {
  color: #212223;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  margin: 0;
}

.message--destination {
  display: flex;
  align-items: center;
  margin: 0;
  opacity: 0.8;
  color: #212223;
  font-size: 12px;
}


.message--preview {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #808080;
  opacity: .8;
  margin: 0;
  font-size: 13px;
  line-height: 20px;
  font-weight: 400;
}

.message--text {
  white-space: pre-line;
  opacity: 0.8;
  color: #212223;
  font-size: 14px;
  font-weight: 400;
}

.message--bar {
  display: flex;
  align-items: center;
  margin-left: auto;
}

.message--bar .twotone-icon {
  margin-left: 20px;
}

.message--date {
  white-space: nowrap;
  color: #808080;
  font-size: 12px;
  line-height: 20px;
  font-weight: 400;
  margin: 0 0 0 30px;
  opacity: 0.8;
  text-transform: capitalize;
}

.message--footer {
  padding: 14px 0 14px 66px;
  border-top: 1px solid #e4e8eb;
}

.message--action_button {
  font-family: Roboto, sans-serif;
  cursor: pointer;
  outline: none;
  display: inline-flex;
  align-items: center;
  border: 1px solid #e4e8eb;
  border-radius: 3px;
  background-color: #ffffff;
  padding: 5px 15px;
  color: #909191;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  margin-right: 18px;
}

.message--action_button svg {
  margin-right: 5px;
}

.message--action_button:hover {
  color: #4285f4;
}

.message--action_button:hover svg {
  fill: #4285f4;
}

.contact-info {
  border-radius: 5px;
  background-color: #ffffff;
  width: 350px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 50;
  display: block;
  text-transform: none;
  font-weight: normal;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  max-width: 0;
  max-height: 0;
  overflow: hidden;
}

.contact-info--header {
  padding: 20px;
  display: flex;
  align-items: flex-start;
}

.contact-info--avatar {
  flex: 0 0 55px;
  height: 55px;
  width: 55px;
  border-radius: 49%;
  background-size: cover;
  background-position: center;
  margin-right: 20px;
}

.contact-info--avatar.no-avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 1;
}
.contact-info--avatar.no-avatar.blue {
  background-color: rgba(66, 133, 244, 0.2);
  border: 1px solid #4285f4;
  color: #4285f4;
}
.contact-info--avatar.no-avatar.yellow {
  background-color: rgba(244, 180, 0, 0.2);
  border: 1px solid #f4b400;
  color: #f4b400;
}
.contact-info--avatar.no-avatar.green {
  background-color: rgba(15, 157, 88, 0.2);
  border: 1px solid #0f9d58;
  color: #0f9d58;
}

.contact-info--name {
  color: #212223;
  font-size: 22px;
  font-weight: 500;
  line-height: 20px;
  margin: 0;
}
.contact-info--email,
.contact-info--phone {
  opacity: 0.8;
  color: #909191;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  margin: 0;
}

.contact-info--body {
  padding: 0 20px 20px;
}

.contact-info--information {
  color: #909191;
  font-size: 14px;
  margin: 0;
  line-height: 24px;
}
.contact-info--information .twotone-icon {
  vertical-align: middle;
  margin-top: -3px;
  margin-right: 15px;
}
.contact-info--link {
  cursor: pointer;
  color: #4285f4;
  margin: 0 5px 0 8px;
}
.contact-info--link.first {
  margin-left: 0;
}
.contact-info--link:hover {
  text-decoration: underline;
}

.contact-info--footer {
  border-top: 1px solid #e4e8eb;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.contact-info--edit {
  cursor: pointer;
  color: #4285f4;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
}
.contact-info--edit:hover {
  text-decoration: underline;
}
.contact-info--footer .twotone-icon {
  margin-top: 2px;
  margin-right: 22px;
}
.contact-info--footer .twotone-icon:last-child {
  margin-right: 0;
  opacity: 0.3;
}

.reply {
  background-color: #ffffff;
  border: 1px solid #e4e8eb;
  border-radius: 3px;
}

.reply--header {
  padding: 18px 25px;
  display: flex;
}
.reply--header .twotone-icon {
  margin-right: 20px;
}
.reply--user {
  color: #212223;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
}


.reply--body {
  padding: 0 66px 20px 66px;
}
.reply--textarea {
  resize: none;
  outline: none;
  min-height: 80px;
  margin-bottom: 20px;
  width: 100%;
  border: none;
  color: #212223;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  line-height: 2;
  opacity: 0.8;
}
.reply--history {
  cursor: pointer;
  display: inline-block;
  height: 10px;
  border-radius: 5px;
  background-color: #e4e8eb;
}
.reply--history .twotone-icon {
  margin-top: -12px;
}
.reply--capture {
  margin-top: 5px;
}
.reply--author {
  color: #212223;
  font-size: 14px;
  font-weight: 500;
  margin: 0;
}
.reply--author-sub {
  color: #909191;
  font-size: 14px;
